    //
    renderpie() {
      let usepublicChartDom, usepublicChartChart, option;
      usepublicChartDom = document.getElementById("usepublic");
      usepublicChartChart = echarts.init(usepublicChartDom);
      var seriesData = [
        {
          name: "",
          value: "40",
          label: {
            color: "#40CA53",
          },
        },
        {
          name: "",
          value: "40",
          label: {
            color: "#40CA53",
          },
