            color: "#EE5CFF",
          },
        },
        {
          name: "",
          value: "10",
          label: {
            color: "#EE5CFF",
          },
        },
        {
          name: "",
          value: "40",
          label: {
            color: "#EE5CFF",
          },
        },
        {
          name: "",
          value: "40",
          label: {
            color: " #31C3FF",
          },
        },
        {
          name: " ",
          value: "40",
          label: {
            color: "#46F4FF",
          },
        },
      ];
      let title = "";
      let formatNumber = function(num) {
        let reg = /(?=(\B)(\d{3})+$)/g;
        return num.toString().replace(reg, ",");
      };
      let total = seriesData.reduce((a, b) => {
        return a + b.value * 1;
      }, 0);
      var legendData = seriesData.map((item) => item.name);
      var colorList = seriesData.map((item) => item.label.color);
      usepublicChartChart.setOption({
        // title: {
        //     text: ``,
        //     x: 'center',
        //     y: 'center',
        //     textStyle: {
        //         color: '#fff'
        //     }
        // },
        title: [
          {
            text:
              "{name|" +
              title +
              "}\n{val|" +
              formatNumber(total) +
              "}{unit|}",
            top: "center",
            left: "center",
            textStyle: {
              rich: {
                name: {
                  fontSize: 12,
                  fontWeight: "normal",
                  color: "rgba(255,255,255,0.8)",
                  padding: [10, 0],
                },
                val: {
                  fontSize: 16,
                  fontWeight: "bold",
                  color: "#00F5FF",
                },
                unit: {
                  fontSize: 12,
                  color: "rgba(255,255,255,0.5)",
                },
              },
            },
          },
        ],
        tooltip: {
          // trigger: 'item',
          // borderColor: 'rgba(255,255,255,.3)',
          backgroundColor: "rgba(13,5,30,.6)",
          borderWidth: 1,
          padding: 5,
          formatter: function(parms) {
            var str =
              parms.marker +
              "" +
              parms.data.name +
              "</br>" +
              // "" + parms.data.value + "</br>" +
              "" +
              parms.percent +
              "%";
            return str;
          },
          textStyle: {
            fontSize: 12,
            color: "rgba(255,255,255,0.8)",
          },
        },
        legend: {
          show: false,
          // type: "scroll",
          orient: "vertical",
          left: "left",
          align: "auto",
          top: "middle",
          textStyle: {
            color: "#fft",
          },
          data: legendData,
        },
        series: [
          {
            type: "pie",
            z: 3,
            center: ["50%", "50%"],
            radius: ["50%", "65%"],
            clockwise: true,
            animationDuration: 2000,
            avoidLabelOverlap: true,
            hoverOffset: 15,
            itemStyle: {
              normal: {
                color: function(params) {
                  return colorList[params.dataIndex];
                },
              },
            },
            label: {
              show: true,
              position: "outside",
            },
            labelLine: {
              normal: {
                length: 20,
                length2: 15,
                lineStyle: {
                  width: 1,
                },
              },
            },
            data: seriesData,
          },
        ],
      });
    },
    //
    toggleBuilds(flag = 0) {
      Array.from(document.querySelectorAll(".buildingtext")).forEach((item) => {
        item.style.opacity = flag;
      });
    },
  },
};
</script>

